import {companiesAPI} from "../api/api";

const SET_COMPANIES = 'SET_COMPANIES'
const SET_COMPANY = 'SET_COMPANY'
const SET_LIST = 'SET_LIST'

let initialState = {
    companies: [],
    company: null,
    list: []
}

const companiesReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_COMPANIES:
            return { ...state, companies: action.companies };
        case SET_COMPANY:
            return { ...state, company: action.company };
        case SET_LIST:
            return { ...state, list: action.list }
        default:
            return state;
    }
}

//action creators
export const setCompanies = (companies) => ({type: SET_COMPANIES, companies});
export const setCompany = (company) => ({type: SET_COMPANY, company});
export const setList = (list) => ({type: SET_LIST, list});

//thunk
export const getMyCompanies = (id) => async (dispatch) => {
    let response = await companiesAPI.getMyCompanies(id);
    dispatch(setCompanies(response));
}

export const getAllCompanies = () => async (dispatch) => {
    let response = await companiesAPI.getAllCompanies();
    dispatch(setList(response));
}

export const getCompanyInfo = (id) => async (dispatch) => {
    let response = await companiesAPI.getCompanyInfo(id);
    dispatch(setCompany(response))
}

export default companiesReducer;
