import {Button, Container, makeStyles, TextField} from "@material-ui/core";
import {useState} from "react";

const useStyles = makeStyles(theme => ({
    login: {
        marginTop: '250px',
        display: 'flex',
        alignItems: 'center',
        flexDirection: 'column',
        padding: '30px',
        backgroundColor: 'rgba(0, 0, 0, 0.03)',
        width: '400px',
        borderRadius: '15px'
    },
    field: {
        marginBottom: '15px',
        width: '300px'
    }
}))

const Login = ({ login,  handleSuccess }) => {
    const styles = useStyles();
    const [email, setEmail] = useState('')
    const [password, setPassword] = useState('')
    const onConfirm = async () => {
        await login(email, password)
        handleSuccess(true, 'Вход успешно выполнен!')
    }
    const handleReset = () => {
        setEmail('')
        setPassword('')
    }
    return (
        <Container style={{ display: 'flex', justifyContent: 'center' }}>
            <div className={styles.login}>
                <TextField value={email} onChange={event => setEmail(event.target.value)} className={styles.field} variant={'outlined'} fullWidth label={'Почта'}/>
                <TextField value={password} onChange={event => setPassword(event.target.value)} type={'password'} className={styles.field} variant={'outlined'} fullWidth label={'Пароль'}/>
                <div>
                    <Button onClick={handleReset} style={{ margin: '0 10px' }} variant={'outlined'} color={'secondary'}>Сброс</Button>
                    <Button onClick={onConfirm} style={{ margin: '0 10px' }} variant={'outlined'} color={'primary'}>Подтвердить</Button>
                </div>
            </div>
        </Container>
    )
}

export default Login;