import {Map, Placemark, YMaps} from "@pbe/react-yandex-maps";
import {Button, Chip, Container, Grid, makeStyles, Typography} from "@material-ui/core";
import {useEffect, useState} from "react";
import clsx from "clsx";
import ChargerInfoContainer from "../ChargerInfo/ChargerInfoContainer";
import {useLocation} from "react-router-dom";

const useStyles = makeStyles(theme => ({
    map: {
        width: '100%'
    },
    list: {
        width: '400px',
        backgroundColor: 'rgba(18,38,114,0.8)',
        borderRadius: '20px',
        padding: '30px'
    },
    listItem: {
        padding: '15px',
        backgroundColor: 'rgba(0, 0, 0, 0.02)',
        transitionDuration: '0.3s',
        borderRadius: '10px',
        cursor: 'pointer',
        marginBottom: '10px'
    },
    listItemTitle: {
        fontWeight: 'lighter'
    },
    address: {
        fontSize: '14px'
    },
    info: {
        position: 'absolute',
        top: 15,
        right: 15,
        zIndex: 2,
        width: '300px',
        backgroundColor: 'white',
        padding: '15px',
        borderRadius: '15px'
    },
    phone: {
        color: 'inherit',
        fontSize: '14px',
    },
    active: {
        backgroundColor: 'rgba(28,70,231,0.82)',
        color: 'white'
    },
    availableDate: {
        display: 'flex',
        alignItems: 'center',
        margin: '15px 0',
        justifyContent: 'space-between'
    },
    chip: {
        borderRadius: '6px',
        border: '8px',
        marginLeft: '15px',
        width: '150px'
    },
    book: {
        backgroundColor: 'green',
        color: 'white',
        '&:hover': {
            backgroundColor: 'green'
        }
    },
    unavailable: {
        color: 'grey',
        textDecoration: 'line-through'
    },
    companyTitle: {
        fontWeight: 'bold',
        fontSize: '48px',
        color: 'white'
    },
    companyProperty: {
        fontWeight: 'bold',
        color: 'white',
        fontSize: '24px'
    },
    coordinate: {
        color: 'white',
        fontStyle: 'italic'
    },
    price: {
        fontSize: '24px',
        fontWeight: 'bold',
        color: 'white'
    },
    actions: {
        margin: '15px 0'
    },
    content: {
        display: 'flex'
    }
}))



const ChargesMap = ({ chargers, getChargers, user }) => {
    const path = useLocation();
    const styles = useStyles();
    useEffect(() => {
        getChargers()
    }, [])
    const [current, setCurrent] = useState(null)
    const handleSetCurrent = (charge) => {
        setCurrent(charge)
    }
    useEffect(() => {
        setCurrent(null)
    }, [])

    return (
        <div className={styles.content}>
            <ChargerInfoContainer id={current ? current.id : null}/>
            <YMaps>
                <Map width={'100%'} height={window.innerHeight * 0.8} defaultState={{center: [43.237793, 76.945690], zoom: 16}}>
                    {chargers.map(x =>
                        <div>
                            <Placemark
                                options={{iconColor: current != null && current.id === x.id ? 'blue' : 'black'}}
                                onClick={() => handleSetCurrent(x)} geometry={[x.latitude, x.longitude]}/>
                        </div>
                    )}
                </Map>
            </YMaps>
        </div>
    )
}

export default ChargesMap;