const UPDATE_SUCCESS = 'UPDATE_SUCCESS'

let initialState = {
    success: false,
    successMessage: null
}

const successReducer = (state = initialState, action) => {
    switch (action.type) {
        case UPDATE_SUCCESS:
            return { ...state, success: action.success, successMessage: action.message };
        default:
            return state;
    }
}

const setSuccess = (success, message) => ({ type: UPDATE_SUCCESS, success, message })

export const handleSuccess = (success, message) => async (dispatch) => {
    dispatch(setSuccess(success, message))
}


export default successReducer;