import {walletsAPI} from "../api/api";

const SET_WALLET = 'SET_WALLET'

let initialState = {
    wallet: null
}

const walletsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_WALLET:
            return { ...state, wallet: action.wallet };
        default:
            return state;
    }
}

//action creators
export const setWallet = (wallet) => ({type: SET_WALLET, wallet});

//thunk
export const getInfo = (id) => async (dispatch) => {
    let response = await walletsAPI.getInfo(id);
    dispatch(setWallet(response));
}


export default walletsReducer;
