import {connect} from "react-redux";
import React from 'react';
import ChargerInfo from "./ChargerInfo";
import {getChargerInfo, startCharging} from "../../redux/chargers-reducer";
import {handleSuccess} from "../../redux/common-reducer";
import login from "../Login/Login";

const ChargerInfoContainer = React.memo((props) => {
    return (
        <ChargerInfo { ...props } />
    )
})

const mapStateToProps = (state) => {
    return {
        chargerInfo: state.chargers.chargerInfo,
        user: state.auth.user
    };
}


export default connect(mapStateToProps, { getChargerInfo, startCharging, handleSuccess, login })(ChargerInfoContainer);
