import {Container, Typography} from "@material-ui/core";
import {useParams} from "react-router-dom";
import {useEffect} from "react";

const Transaction = ({ getInfo, transaction }) => {
    const { id } = useParams();
    useEffect(() => {
        if (id != null) getInfo(id)
    }, [id])
    if (transaction == null) return null
    return (
        <Container>
            <Typography variant={'h4'}>Сумма транзакции: {transaction.sum} KZT</Typography>
            <Typography>Дата: {new Date(Date.parse(transaction.date)).toLocaleString()}</Typography>
            <Typography>Пользователь: {transaction.user}</Typography>
        </Container>
    )
}

export default Transaction;