import {connect} from "react-redux";
import React from 'react';
import ChargesMap from "./ChargesMap";
import {getChargers} from "../../redux/chargers-reducer";

const ChargesMapContainer = React.memo((props) => {
    return (
        <ChargesMap { ...props } />
    )
})

const mapStateToProps = (state) => {
    return {
        user: state.auth.user,
        chargers: state.chargers.chargers
    };
}


export default connect(mapStateToProps, { getChargers })(ChargesMapContainer);
