import {connect} from "react-redux";
import React from 'react';
import {getInfo} from "../../redux/transactions-reducer";
import Transaction from "./Transaction";

const TransactionContainer = React.memo((props) => {
    return (
        <Transaction { ...props } />
    )
})

const mapStateToProps = (state) => {
    return {
        user: state.auth.user,
        transaction: state.transactions.transaction
    };
}


export default connect(mapStateToProps, { getInfo })(TransactionContainer);
