import {Button, Chip, CircularProgress, Fab, makeStyles, Typography} from "@material-ui/core";
import {useEffect, useState} from "react";
import LocationOnIcon from '@material-ui/icons/LocationOn';
import LocalAtmIcon from '@material-ui/icons/LocalAtm';
import PowerSettingsNewIcon from '@material-ui/icons/PowerSettingsNew';
import clsx from "clsx";
import {useHistory} from "react-router-dom";

const useStyles = makeStyles(theme => ({
    title: {
        fontSize: '36px',
        fontWeight: 'bold'
    },
    description: {
        fontSize: '24px',
    },
    company: {
        fontSize: '18px',
    },
    coordinates: {
        display: 'flex',
        alignItems: 'center',
        margin: '10px 0'
    },
    coordinatesValue: {
        marginLeft: '10px'
    },
    image: {
        borderRadius: '15px'
    },
    imageWrapper: {
        display: 'flex',
        width: '200px',
        height: '200px',
        justifyContent: 'center',
        alignItems: 'center',
        borderRadius: '15px',
        backgroundColor: 'grey',
        color: 'white',
        fontWeight: 'bold',
        fontSize: '28px',
        fontStyle: 'italic'
    },
    chip: {
        borderRadius: '6px',
        padding: '8px',
        transitionDuration: '0.3s',
        backgroundColor: 'rgba(81,232,37,0.43)',
        color: 'rgba(17,80,22,0.8)',
        fontWeight: 'bold'
    },
    actions: {
        padding: '15px',

    },
    info: {
        padding: '0 50px',
        minWidth: '300px'
    },
    new: {
        backgroundColor: 'rgba(239,41,41,0.42)',
        color: 'rgba(131,19,19,0.8)',
        fontWeight: 'bold'
    },
    awaitingConnector: {
        backgroundColor: 'rgba(239,160,41,0.42)',
        color: 'rgba(133,83,11,0.8)',
        fontWeight: 'bold'
    },
    awaitingFlow: {
        backgroundColor: 'rgba(183,239,41,0.42)',
        color: 'rgba(84,112,13,0.8)',
        fontWeight: 'bold'
    },
    charging: {
        backgroundColor: 'rgba(41,239,51,0.42)',
        color: 'rgba(10,87,15,0.8)',
        fontWeight: 'bold'
    },
    chargingCompleted: {
        backgroundColor: 'rgba(41,239,120,0.42)',
        color: 'rgba(4,112,103,0.8)',
        fontWeight: 'bold'
    },
    awaitingConnectorRemove: {
        backgroundColor: 'rgba(41,203,239,0.42)',
        color: 'rgba(6,80,98,0.8)',
        fontWeight: 'bold'
    },
    finished: {
        backgroundColor: 'rgba(102,156,245,0.42)',
        color: 'rgba(2,54,128,0.8)',
        fontWeight: 'bold'
    },
    logs: {
        padding: '20px'
    },
    log: {
        marginBottom: '10px'
    }
}))

const ChargerInfo = ({ id, getChargerInfo, chargerInfo, startCharging, user, handleSuccess, login }) => {
    const styles = useStyles();
    const history = useHistory();
    if (user.roles.some(x => x.title === "CompanyOwner")) {
        history.push('/')
    }
    useEffect(() => {
        const getAPIdata = async () => {
            await getChargerInfo(id)
        }

        if (id != null) getAPIdata()

        const interval = setInterval(() => {
            if (id != null) getAPIdata()
        }, 400)
        return () => clearInterval(interval)
    }, [id])

    const getLabel = (status) => {
        switch (status) {
            case 0:
                return 'Новая сессия'
            case 1:
                return 'Ожидание коннектора'
            case 2:
                return 'Ожидание подачи электричества'
            case 3:
                return 'Зарядка'
            case 4:
                return 'Зарядка завершена'
            case 5:
                return 'Коннектор извлечен'
            case 6:
                return 'Сессия закончена'
        }
    }

    const handleStart = async () => {
        if (user != null && chargerInfo != null) {
            await startCharging(chargerInfo.id, user.id)
        }
    }

    if (id == null) return null
    if (chargerInfo == null) return null


    return (
        <div className={styles.info}>
            <div className={styles.imageWrapper}>
                { chargerInfo.title }
            </div>
            <Typography className={styles.title}>{chargerInfo.title}</Typography>
            <Typography className={styles.company}>{chargerInfo.company} company</Typography>
            <Typography className={styles.description}>{chargerInfo.description}</Typography>
            <div className={styles.coordinates}>
                <LocationOnIcon/>
                <Typography className={styles.coordinatesValue}>{chargerInfo.latitude}, {chargerInfo.longitude}</Typography>
            </div>
            <div className={styles.coordinates}>
                <LocalAtmIcon/>
                <Typography className={styles.coordinatesValue}>{chargerInfo.pricePerMinute} KZT/MIN</Typography>
            </div>
            { chargerInfo.currentSession == null ? <Chip className={styles.chip} label={'Станция доступна'}/> :
                <div>
                    <Chip className={clsx({
                            [styles.new]: chargerInfo.currentSession.status === 0,
                            [styles.awaitingConnector]: chargerInfo.currentSession.status === 1,
                            [styles.awaitingFlow]: chargerInfo.currentSession.status === 2,
                            [styles.charging]: chargerInfo.currentSession.status === 3,
                            [styles.chargingCompleted]: chargerInfo.currentSession.status === 4,
                            [styles.awaitingConnectorRemove]: chargerInfo.currentSession.status === 5,
                            [styles.finished]: chargerInfo.currentSession.status === 6,
                        }, styles.chip
                    )} label={getLabel(chargerInfo.currentSession.status)}/>
                    <div className={styles.logs}>
                        {
                            chargerInfo.currentSession.logs.map(x =>
                                <div className={styles.log}>
                                    <Typography>{new Date(Date.parse(x.createdAt)).toLocaleString()}: {x.message}</Typography>
                                </div>
                            )
                        }
                    </div>
                </div>
            }
            <div className={styles.actions}>
                {
                    chargerInfo.currentSession && chargerInfo.currentSession.status !== 6 ?
                        <Fab style={{ backgroundColor: 'green' }}>
                            <CircularProgress style={{ color: 'white' }}/>
                        </Fab> :
                        <Fab onClick={handleStart} style={{ backgroundColor: 'green' }}>
                            <div>
                                <PowerSettingsNewIcon style={{color: 'white'}}/>
                            </div>
                        </Fab>
                }
            </div>
        </div>
    )
}

