import {transactionsAPI} from "../api/api";

const SET_TRANSACTION = 'SET_TRANSACTION'

let initialState = {
    transaction: null
}

const transactionsReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_TRANSACTION:
            return { ...state, transaction: action.transaction };
        default:
            return state;
    }
}

//action creators
export const setTransaction = (transaction) => ({type: SET_TRANSACTION, transaction});

//thunk
export const getInfo = (id) => async (dispatch) => {
    let response = await transactionsAPI.getInfo(id);
    dispatch(setTransaction(response));
}


export default transactionsReducer;
