import {Button, Chip, Container, makeStyles, Typography} from "@material-ui/core";
import {useHistory} from "react-router-dom";
import {useEffect} from "react";
import {DataGrid} from "@mui/x-data-grid";
import clsx from "clsx";



const useStyles = makeStyles(theme => ({
    company: {
        padding: '15px',
        width: '250px',
        backgroundColor: 'rgba(0, 0, 0, 0.05)',
        borderRadius: '10px',
        height: '150px',
        margin: '15px',
        transitionDuration: '0.3s',
        '&:hover': {
            backgroundColor: 'rgba(28,70,231,0.82)',
            color: 'white'
        },
        cursor: 'pointer'
    },
    companies: {
        display: 'flex',
        alignItems: 'center',
        flexWrap: 'wrap'
    },
    title: {
        fontSize: '24px',
        fontWeight: 'bold'
    },
    description: {
        fontSize: '16px'
    }
}))

const Companies = ({ companies, getMyCompanies, user }) => {
    const styles = useStyles();
    const history = useHistory();

    const handleGoToCompanyChargers = (id) => {
        if (id) history.push(`/companies/chargers/${id}`);
    }

    const columns = [
        { field: 'title', headerName: 'Название', width: 170, renderCell: (params) => { return <Typography>{params.row.title}</Typography> } },
        { field: 'description', headerName: 'Описание', width: 180, renderCell: (params) => { return <Typography>{params.row.description}</Typography> } },
        { field: 'chargersCount', headerName: 'Количество станций', width: 250, renderCell: (params) => { return <Typography>{params.row.chargersCount}</Typography> } },
        { field: 'createdAt', headerName: 'Дата создания', width: 170, renderCell: (params) => { return <Typography>{new Date(Date.parse(params.row.createdAt)).toLocaleString()}</Typography> } },
        { field: 'updatedAt', headerName: 'Дата обновления', width: 170, renderCell: (params) => { return <Typography>{new Date(Date.parse(params.row.updatedAt)).toLocaleString()}</Typography> } },
        { field: 'actions', headerName: 'Действия', align: 'center', width: 220, renderCell: (params) => { return <div>
                <Button onClick={() => handleGoToCompanyChargers(params.row.id)} variant={'outlined'} color={'primary'}>Управление</Button>
            </div> } },
    ]

    useEffect(() => {
        getMyCompanies(user.id)
    }, [])

    return (
        <Container className={styles.companies}>
            <DataGrid autoHeight columns={columns} rows={companies}/>
        </Container>
    )
}

export default Companies;