import {Route, Switch as RouteSwitch} from "react-router-dom";
import {Container, makeStyles, Snackbar, Typography} from "@material-ui/core";
import {withSuspense} from "../../HOC/withSuspense";
import {Alert} from "@material-ui/lab";
import ChargesMapContainer from "../Map/ChargesMapContainer";
import LoginContainer from "../Login/LoginContainer";
import CompaniesContainer from "../Companies/CompaniesContainer";
import CompanyChargersContainer from "../CompanyChargers/CompanyChargersContainer";
import ChargerContainer from "../Charger/ChargerContainer";
import WalletContainer from "../Wallet/WalletContainer";
import SessionContainer from "../Session/SessionContainer";
import TransactionContainer from "../Transaction/TransactionContainer";

const useStyles = makeStyles(theme => ({
    content: {
        marginTop: '120px'
    }
}))

const Content = ({ successMessage, success, handleSuccess, user }) => {
    const styles = useStyles();
    if (user === null) return <LoginContainer/>
    return (
        <div className={styles.content}>
            <RouteSwitch>
                <Route path={'/map'} render={withSuspense(ChargesMapContainer)}/>
                <Route exact path={'/chargers/:id'} render={withSuspense(ChargerContainer)}/>
                <Route exact path={'/companies/chargers/:id'} render={withSuspense(CompanyChargersContainer)}/>
                <Route exact path={'/sessions/:id'} render={withSuspense(SessionContainer)}/>
                <Route exact path={'/transactions/:id'} render={withSuspense(TransactionContainer)}/>
                <Route path={'/companies'} render={withSuspense(CompaniesContainer)}/>
                <Route path={'/wallet'} render={withSuspense(WalletContainer)}/>
                <Route path={'/'} render={() => <></>}/>
            </RouteSwitch>
            <Snackbar open={success} autoHideDuration={5000} onClose={() => handleSuccess(false)}>
                <Alert variant={'filled'} onClose={() => handleSuccess(false)} severity="success">
                    {successMessage}
                </Alert>
            </Snackbar>
        </div>
    )
}

export default Content;