import axios from "axios";

const ENV = "DEV";

const getUrl = (ENV) => {
    switch (ENV) {
        case "DEV":
            return "http://www.chargerskz.somee.com/"
        case "PROD":
            return "http://172.19.26.205:88/"
    }
}

const BASE_URL = getUrl(ENV);



const instance = axios.create({
    baseURL: BASE_URL,
    withCredentials: true
})

export const authAPI = {
    me() {
        return instance.get(`auth`).then(response => response.data);
    },
    login(email, password) {
        return instance.get(`users/login?email=${email}&password=${password}`).then(response => response.data);
    }
}

export const companiesAPI = {
    getMyCompanies(id) {
        return instance.get(`companies/getUserCompanies?userId=${id}`).then(response => response.data)
    },
    getCompanyInfo(id) {
        return instance.get(`companies/getCompanyInfo?companyId=${id}`).then(response => response.data)
    },
    getAllCompanies() {
        return instance.get(`companies`).then(response => response.data)
    }
}

export const walletsAPI = {
    getInfo(id) {
        return instance.get(`wallets/getInfo?userId=${id}`).then(response => response.data)
    }
}

export const sessionsAPI = {
    getInfo(id) {
        return instance.get(`sessions?sessionId=${id}`).then(response => response.data)
    }
}

export const transactionsAPI = {
    getInfo(id) {
        return instance.get(`transactions?transactionId=${id}`).then(response => response.data)
    }
}

export const chargersAPI = {
    getChargers() {
        return instance.get(`chargers`).then(response => response.data)
    },
    getCompanyChargers(id) {
        return instance.get(`chargers/getCompanyChargers?companyId=${id}`).then(response => response.data)
    },
    getChargerInfo(id) {
        return instance.get(`chargers/getChargerInfo?chargerId=${id}`).then(response => response.data)
    },
    getChargerProfile(id) {
        return instance.get(`chargers/getChargerProfile?chargerId=${id}`).then(response => response.data)
    },
    startCharging(id, userId) {
        return instance.post(`chargers/initializeCharging`, { chargerId: id, userId: userId }).then(response => response.data)
    }
}
