import {connect} from "react-redux";
import React from 'react';
import Session from "./Session";
import {getInfo} from "../../redux/sessions-reducer";

const SessionContainer = React.memo((props) => {
    return (
        <Session { ...props } />
    )
})

const mapStateToProps = (state) => {
    return {
        user: state.auth.user,
        session: state.sessions.session
    };
}


export default connect(mapStateToProps, { getInfo })(SessionContainer);
