import {Button, Container, Typography} from "@material-ui/core";
import {useEffect} from "react";
import {DataGrid} from "@mui/x-data-grid";
import {useHistory} from "react-router-dom";

const Wallet = ({ wallet, getInfo, user }) => {
    useEffect(() => {
        if (user != null) {
            getInfo(user.id)
        }
    }, [user])
    const history = useHistory();
    const handleGoToPage = (page) => {
        history.push(page)
    }
    if (wallet == null) return null


    const columns = [
        { field: 'date', headerName: 'Дата', width: 350, renderCell: (params) => { return <Typography>{new Date(Date.parse(params.row.date)).toLocaleString()}</Typography> } },
        { field: 'sum', headerName: 'Сумма', width: 600, renderCell: (params) => { return <Typography>{params.row.sum} KZT</Typography> } },
        { field: 'sessionId', headerName: 'Сессия', align: 'center', width: 250, renderCell: (params) => { return <Button onClick={() => handleGoToPage(`/sessions/${params.row.sessionId}`)} variant={'outlined'} color={'primary'}>Перейти к сессии</Button> } },

    ]
    return (
        <Container>
            <div>
                <Typography variant={'h5'}>Кошелек пользователя {wallet.fullName}</Typography>
                <Typography>Баланс {wallet.balance} KZT</Typography>
            </div>
            <div>
                <Typography>Транзакции пользователя:</Typography>
                <DataGrid autoHeight columns={columns} rows={wallet.transactions}/>
            </div>
        </Container>
    )
}


export default Wallet;