import {connect} from "react-redux";
import React from 'react';
import Charger from "./Charger";
import {getChargerProfile} from "../../redux/chargers-reducer";

const ChargerContainer = React.memo((props) => {
    return (
        <Charger { ...props } />
    )
})

const mapStateToProps = (state) => {
    return {
        user: state.auth.user,
        chargerProfile: state.chargers.chargerProfile
    };
}


export default connect(mapStateToProps, { getChargerProfile })(ChargerContainer);
