import {AppBar, Avatar, Button, Container, makeStyles, Tooltip, Typography} from "@material-ui/core";
import LocationOnIcon from '@material-ui/icons/LocationOn';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import {NavLink, useHistory} from "react-router-dom";
import {useEffect} from "react";


const useStyles = makeStyles(theme => ({
    header: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        padding: '30px',
    },
    leftBlock: {
        display: 'flex',
        alignItems: 'center'
    },
    links: {
        marginLeft: '30px',
        display: 'flex',
        alignItems: 'center',
    },
    rightBlock: {
        display: 'flex',
        alignItems: 'center'
    },
    link: {
        color: 'white',
        textDecoration: 'none',
        marginRight: '15px',
        cursor: 'pointer'
    },
    balance: {
        margin: '0 15px',
        fontWeight: 'bold'
    },
    avatar: {
        cursor: 'pointer'
    }
}))



const Header = ({ user, logout, handleSuccess, login, chargerInfo }) => {
    const getFullName = (user) => {
        return `${user.surname} ${user.firstName} ${user.fatherName}`
    }
    const onLogout = async () => {
        await logout()
        handleSuccess(true, "Выход выполнен успешно!")
    }

    const history = useHistory();
    const handleGoToPage = (page) => {
        history.push(page)
    }
    const styles = useStyles();

    useEffect(() => {
        if (user == null) login(null, null)
    }, [])


    useEffect(() => {
        if (user != null) {
            if (chargerInfo != null) {
                if (chargerInfo.currentSession != null) {
                    if (chargerInfo.currentSession.status === 5) {
                        login(user.email, user.password)
                        handleSuccess(true, 'Зарядка успешно выполнена!')
                    }
                }
            }
        }
    }, [chargerInfo])


    return (
        <AppBar>
            <Container>
                <div className={styles.header}>
                    <div className={styles.leftBlock}>
                        <Typography variant={'h4'}>Chargers</Typography>
                        <div className={styles.links}>
                            <Typography className={styles.link} onClick={() => handleGoToPage('/')}>Главная страница</Typography>
                            { user && user.roles.some(z => z.title === 'CompanyOwner') && <Typography className={styles.link} onClick={() => handleGoToPage('/companies')}>Управление компаниями</Typography> }
                            { user && !user.roles.some(z => z.title === 'CompanyOwner') && <Typography className={styles.link} onClick={() => handleGoToPage('/map')}>Карта</Typography> }
                        </div>
                    </div>
                    <div className={styles.rightBlock}>
                        <div style={{ display: 'flex', marginRight: '15px' }}>
                            <LocationOnIcon/>
                            <Typography>Almaty</Typography>
                            <ExpandMoreIcon/>
                        </div>
                        <div style={{ display: 'flex', marginRight: '15px' }}>
                            <Typography>RU</Typography>
                            <ExpandMoreIcon/>
                        </div>
                        { user && <Tooltip title={<Typography>{getFullName(user)}</Typography>}><Avatar className={styles.avatar} onClick={() => handleGoToPage('/wallet')}/></Tooltip> }
                        { user && <Typography className={styles.balance}>{user.balance} тг.</Typography> }
                        { user && <Button onClick={onLogout} style={{ marginLeft: '15px' }} variant={'contained'} color={'secondary'}>Выйти</Button> }
                    </div>
                </div>
            </Container>
        </AppBar>
    )
}

export default Header;