import {Button, Chip, Container, IconButton, makeStyles, Tooltip, Typography} from "@material-ui/core";
import {useHistory, useParams} from "react-router-dom";
import {useEffect} from "react";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import LocalAtmIcon from "@material-ui/icons/LocalAtm";
import InfoIcon from '@material-ui/icons/Info';
import {Map, Placemark, YMaps} from "@pbe/react-yandex-maps";
import {DataGrid} from "@mui/x-data-grid";
import clsx from "clsx";

const useStyles = makeStyles(theme =>({
    title: {
        fontSize: '36px',
        fontWeight: 'bold'
    },
    description: {
        fontSize: '24px',
    },
    company: {
        fontSize: '18px',
    },
    coordinates: {
        display: 'flex',
        alignItems: 'center',
        margin: '10px 0'
    },
    coordinatesValue: {
        marginLeft: '10px'
    },
    image: {
        borderRadius: '15px'
    },
    imageWrapper: {
        display: 'flex'
    },
    chip: {
        borderRadius: '6px',
        padding: '8px',
        transitionDuration: '0.3s',
        backgroundColor: 'rgba(81,232,37,0.43)',
        color: 'rgba(17,80,22,0.8)',
        fontWeight: 'bold'
    },
    actions: {
        padding: '15px',

    },
    info: {
        padding: '0 50px'
    },
    content: {

    },
    infoWrapper: {
        display: 'flex'
    },
    new: {
        backgroundColor: 'rgba(239,41,41,0.42)',
        color: 'rgba(131,19,19,0.8)',
        fontWeight: 'bold'
    },
    awaitingConnector: {
        backgroundColor: 'rgba(239,160,41,0.42)',
        color: 'rgba(133,83,11,0.8)',
        fontWeight: 'bold'
    },
    awaitingFlow: {
        backgroundColor: 'rgba(183,239,41,0.42)',
        color: 'rgba(84,112,13,0.8)',
        fontWeight: 'bold'
    },
    charging: {
        backgroundColor: 'rgba(41,239,51,0.42)',
        color: 'rgba(10,87,15,0.8)',
        fontWeight: 'bold'
    },
    chargingCompleted: {
        backgroundColor: 'rgba(41,239,120,0.42)',
        color: 'rgba(4,112,103,0.8)',
        fontWeight: 'bold'
    },
    awaitingConnectorRemove: {
        backgroundColor: 'rgba(41,203,239,0.42)',
        color: 'rgba(6,80,98,0.8)',
        fontWeight: 'bold'
    },
    finished: {
        backgroundColor: 'rgba(102,156,245,0.42)',
        color: 'rgba(2,54,128,0.8)',
        fontWeight: 'bold'
    },
}))

const Charger = ({ getChargerProfile, chargerProfile }) => {
    const { id } = useParams();
    useEffect(() => {
        if (id != null) getChargerProfile(id)
    }, [id])
    const styles = useStyles();

    const getLabel = (status) => {
        switch (status) {
            case 0:
                return 'Новая сессия'
            case 1:
                return 'Ожидание коннектора'
            case 2:
                return 'Ожидание подачи электричества'
            case 3:
                return 'Зарядка'
            case 4:
                return 'Зарядка завершена'
            case 5:
                return 'Коннектор извлечен'
            case 6:
                return 'Сессия закончена'
        }
    }
    const history = useHistory();
    const handleGoToPage = (page) => {
        history.push(page)
    }
    const columns = [
        { field: 'startDate', headerName: 'Дата начала', width: 200, renderCell: (params) => { return <Typography>{new Date(Date.parse(params.row.startDate)).toLocaleString()}</Typography> } },
        { field: 'endDate', headerName: 'Дата завершения', width: 200, renderCell: (params) => { return <Typography>{new Date(Date.parse(params.row.endDate)).toLocaleString()}</Typography> } },
        { field: 'sum', headerName: 'Сумма', width: 150, renderCell: (params) => { return <Typography>{params.row.sum} KZT</Typography> } },
        { field: 'transactionId', align: 'center', headerName: 'Транзакция', width: 310, renderCell: (params) => { return <div>{params.row.transactionId !== '00000000-0000-0000-0000-000000000000' &&
                <Button color={'primary'} variant={'outlined'} onClick={() => handleGoToPage(`/transactions/${params.row.transactionId}`)}>Перейти к транзакции</Button>
        }</div> } },
        { field: 'status', headerName: 'Статус', width: 200, renderCell: (params) => { return (
                <Chip className={clsx({
                        [styles.new]: params.row.status === 0,
                        [styles.awaitingConnector]: params.row.status === 1,
                        [styles.awaitingFlow]: params.row.status === 2,
                        [styles.charging]: params.row.status === 3,
                        [styles.chargingCompleted]: params.row.status === 4,
                        [styles.awaitingConnectorRemove]: params.row.status === 5,
                        [styles.finished]: params.row.status === 6,
                    }, styles.chip
                )} label={getLabel(params.row.status)}/>
                )
        } },
        { field: 'logs', headerName: 'Логи', width: 150, renderCell: (params) => { return <Tooltip title={
            <div>
                {params.row.logs.map(x =>
                    <Typography>{new Date(Date.parse(x.createdAt)).toLocaleString()} - {x.message}</Typography>
                )}
            </div>
            }>
                <IconButton>
                    <InfoIcon/>
                </IconButton>
            </Tooltip> } },

    ]

    return (
        <Container className={styles.content}>
            <div className={styles.infoWrapper}>
                {
                    chargerProfile &&
                    <div className={styles.info}>
                        <div className={styles.imageWrapper}>
                            <img className={styles.image} src={'https://picsum.photos/200'}/>
                        </div>
                        <Typography className={styles.title}>{chargerProfile.title}</Typography>
                        <Typography className={styles.description}>{chargerProfile.description}</Typography>
                        <div className={styles.coordinates}>
                            <LocationOnIcon/>
                            <Typography className={styles.coordinatesValue}>{chargerProfile.latitude}, {chargerProfile.longitude}</Typography>
                        </div>
                        <div className={styles.coordinates}>
                            <LocalAtmIcon/>
                            <Typography className={styles.coordinatesValue}>{chargerProfile.pricePerMinute} KZT/MIN</Typography>
                        </div>
                    </div>
                }
                {
                    chargerProfile &&
                    <YMaps>
                        <Map width={'100%'} height={window.innerHeight * 0.4} defaultState={{center: [chargerProfile.latitude, chargerProfile.longitude], zoom: 16}}>
                            <Placemark
                                geometry={[chargerProfile.latitude, chargerProfile.longitude]}/>
                        </Map>
                    </YMaps>
                }
            </div>
            {
                chargerProfile &&
                <DataGrid autoHeight columns={columns} rows={chargerProfile.sessions}/>
            }
        </Container>
    )
}

export default Charger;