import {chargersAPI} from "../api/api";

const SET_CHARGERS = 'SET_CHARGERS'
const SET_COMPANY_CHARGERS = 'SET_COMPANY_CHARGERS'
const SET_CHARGER_INFO = 'SET_CHARGER_INFO'
const SET_CHARGER_PROFILE = 'SET_CHARGER_PROFILE'


let initialState = {
    chargers: [],
    companyChargers: [],
    chargerInfo: null,
    chargerProfile: null
}

const chargersReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_CHARGERS:
            return { ...state, chargers: action.chargers };
        case SET_COMPANY_CHARGERS:
            return { ...state, companyChargers: action.companyChargers };
        case SET_CHARGER_INFO:
            return { ...state, chargerInfo: action.chargerInfo }
        case SET_CHARGER_PROFILE:
            return { ...state, chargerProfile: action.chargerProfile }
        default:
            return state;
    }
}

//action creators
export const setChargers = (chargers) => ({type: SET_CHARGERS, chargers});
export const setCompanyChargers = (companyChargers) => ({ type: SET_COMPANY_CHARGERS, companyChargers })
export const setChargerInfo = (chargerInfo) => ({ type: SET_CHARGER_INFO, chargerInfo })
export const setChargerProfile = (chargerProfile) => ({ type: SET_CHARGER_PROFILE, chargerProfile })

//thunk
export const getChargers = () => async (dispatch) => {
    let response = await chargersAPI.getChargers();
    dispatch(setChargers(response));
}

export const getCompanyChargers = (id) => async (dispatch) => {
    let response = await chargersAPI.getCompanyChargers(id);
    dispatch(setCompanyChargers(response));
}

export const startCharging = (id, userId) => async (dispatch) => {
    let response = await chargersAPI.startCharging(id, userId);
}

export const getChargerInfo = (id) => async (dispatch) => {
    let response = await chargersAPI.getChargerInfo(id)
    dispatch(setChargerInfo(response))
}

export const getChargerProfile = (id) => async (dispatch) => {
    let response = await chargersAPI.getChargerProfile(id)
    dispatch(setChargerProfile(response))
}

export default chargersReducer;
