import {Container, makeStyles, Typography} from "@material-ui/core";
import {useParams} from "react-router-dom";
import {useEffect} from "react";
import LocationOnIcon from "@material-ui/icons/LocationOn";
import LocalAtmIcon from "@material-ui/icons/LocalAtm";
import {Map, Placemark, YMaps} from "@pbe/react-yandex-maps";

const useStyles = makeStyles(theme => ({
    title: {
        fontSize: '36px',
        fontWeight: 'bold'
    },
    description: {
        fontSize: '24px',
    },
    company: {
        fontSize: '18px',
    },
    coordinates: {
        display: 'flex',
        alignItems: 'center',
        margin: '10px 0'
    },
    coordinatesValue: {
        marginLeft: '10px'
    },
    image: {
        borderRadius: '15px'
    },
    imageWrapper: {
        display: 'flex'
    },
    chip: {
        borderRadius: '6px',
        padding: '8px',
        transitionDuration: '0.3s',
        backgroundColor: 'rgba(81,232,37,0.43)',
        color: 'rgba(17,80,22,0.8)',
        fontWeight: 'bold'
    },
    actions: {
        padding: '15px',

    },
    info: {
        padding: '0 50px'
    },
    new: {
        backgroundColor: 'rgba(239,41,41,0.42)',
        color: 'rgba(131,19,19,0.8)',
        fontWeight: 'bold'
    },
    awaitingConnector: {
        backgroundColor: 'rgba(239,160,41,0.42)',
        color: 'rgba(133,83,11,0.8)',
        fontWeight: 'bold'
    },
    awaitingFlow: {
        backgroundColor: 'rgba(183,239,41,0.42)',
        color: 'rgba(84,112,13,0.8)',
        fontWeight: 'bold'
    },
    charging: {
        backgroundColor: 'rgba(41,239,51,0.42)',
        color: 'rgba(10,87,15,0.8)',
        fontWeight: 'bold'
    },
    chargingCompleted: {
        backgroundColor: 'rgba(41,239,120,0.42)',
        color: 'rgba(4,112,103,0.8)',
        fontWeight: 'bold'
    },
    awaitingConnectorRemove: {
        backgroundColor: 'rgba(41,203,239,0.42)',
        color: 'rgba(6,80,98,0.8)',
        fontWeight: 'bold'
    },
    finished: {
        backgroundColor: 'rgba(102,156,245,0.42)',
        color: 'rgba(2,54,128,0.8)',
        fontWeight: 'bold'
    },
    logs: {
        padding: '20px'
    },
    log: {
        marginBottom: '10px'
    },
    infoWrapper: {
        display: 'flex'
    },
}))

const Session = ({ getInfo, session }) => {
    const { id } = useParams();
    useEffect(() => {
        if (id != null) getInfo(id)
    }, [id])
    const styles = useStyles();
    if (session == null) return null

    return (
        <Container>
            <div className={styles.infoWrapper}>
                {
                    session.charger &&
                    <div className={styles.info}>
                        <div className={styles.imageWrapper}>
                            <img className={styles.image} src={'https://picsum.photos/200'}/>
                        </div>
                        <Typography className={styles.title}>{session.charger.title}</Typography>
                        <Typography className={styles.description}>{session.charger.description}</Typography>
                        <div className={styles.coordinates}>
                            <LocationOnIcon/>
                            <Typography className={styles.coordinatesValue}>{session.charger.latitude}, {session.charger.longitude}</Typography>
                        </div>
                        <div className={styles.coordinates}>
                            <LocalAtmIcon/>
                            <Typography className={styles.coordinatesValue}>{session.charger.pricePerMinute} KZT/MIN</Typography>
                        </div>
                    </div>
                }
                <YMaps>
                        <Map width={'100%'} height={window.innerHeight * 0.4} defaultState={{center: [session.charger.latitude, session.charger.longitude], zoom: 16}}>
                            <Placemark
                                geometry={[session.charger.latitude, session.charger.longitude]}/>
                        </Map>
                </YMaps>

            </div>

            <div className={styles.logs}>
                {
                    session.logs.map(x =>
                        <div className={styles.log}>
                            <Typography>{new Date(Date.parse(x.createdAt)).toLocaleString()}: {x.message}</Typography>
                        </div>
                    )
                }
            </div>
        </Container>
    )
}

export default Session;