import {connect} from "react-redux";
import React from 'react';
import Wallet from "./Wallet";
import {getInfo} from "../../redux/wallets-reducer";

const WalletContainer = React.memo((props) => {
    return (
        <Wallet { ...props } />
    )
})

const mapStateToProps = (state) => {
    return {
        user: state.auth.user,
        wallet: state.wallets.wallet
    };
}


export default connect(mapStateToProps, { getInfo })(WalletContainer);
