import {connect} from "react-redux";
import React from 'react';
import Header from "./Header";
import {login, logout} from "../../redux/auth-reducer";
import {handleSuccess} from "../../redux/common-reducer";

const HeaderContainer = React.memo((props) => {
    return (
        <Header { ...props } />
    )
})

const mapStateToProps = (state) => {
    return {
        user: state.auth.user,
        chargerInfo: state.chargers.chargerInfo
    };
}


export default connect(mapStateToProps, { logout, login, handleSuccess })(HeaderContainer);
