import {Button, Chip, Container, makeStyles, Typography} from "@material-ui/core";
import {useHistory, useParams} from "react-router-dom";
import {useEffect} from "react";
import {DataGrid} from "@mui/x-data-grid";
import clsx from "clsx";



const useStyles = makeStyles(theme => ({

}))

const CompanyChargers = ({ companyChargers, getCompanyChargers, user }) => {
    const styles = useStyles();
    const history = useHistory();
    const { id } = useParams();

    const handleGoToCharger = (id) => {
        if (id) history.push(`/chargers/${id}`);
    }

    const columns = [
        { field: 'title', headerName: 'Название', width: 150, renderCell: (params) => { return <Typography>{params.row.title}</Typography> } },
        { field: 'description', headerName: 'Описание', width: 150, renderCell: (params) => { return <Typography>{params.row.description}</Typography> } },
        { field: 'sessions', headerName: 'Количество сессий', width: 200, renderCell: (params) => { return <Typography>{params.row.sessions}</Typography> } },
        { field: 'pricePerMinute', headerName: 'Стоимость минуты', width: 210, renderCell: (params) => { return <Typography>{params.row.pricePerMinute} тг.</Typography> } },
        { field: 'longitude', headerName: 'Широта', width: 150, renderCell: (params) => { return <Typography>{params.row.longitude}</Typography> } },
        { field: 'latitude', headerName: 'Долгота', width: 150, renderCell: (params) => { return <Typography>{params.row.latitude}</Typography> } },
        { field: 'actions', headerName: 'Действия', align: 'center', width: 220, renderCell: (params) => { return <div>
                <Button onClick={() => handleGoToCharger(params.row.id)} variant={'outlined'} color={'primary'}>Управление</Button>
            </div> } },
    ]

    useEffect(() => {
        if (id != null) getCompanyChargers(id)
    }, [id])

    return (
        <Container className={styles.chargers}>
            <DataGrid autoHeight columns={columns} rows={companyChargers}/>
        </Container>
    )
}

export default CompanyChargers;