import {connect} from "react-redux";
import React from 'react';
import CompanyChargers from "./CompanyChargers";
import {getCompanyChargers} from "../../redux/chargers-reducer";

const CompanyChargersContainer = React.memo((props) => {
    return (
        <CompanyChargers { ...props } />
    )
})

const mapStateToProps = (state) => {
    return {
        companyChargers: state.chargers.companyChargers
    };
}


export default connect(mapStateToProps, { getCompanyChargers })(CompanyChargersContainer);
